header {
  width: 100%;
  border-bottom: 1px solid $yellowcolor;
  margin-bottom: 30px;

  nav {
    .cont {
      display: flex;
      justify-content: flex-start;
      background: azure;
      width: 100%;

      @include for-phone {
        display: block;
        text-align: center;
        padding: 0;
        margin: 0;
        // height: 150px;
      }
    }
    div {
      .logo {
        margin-right: 20px;
        height: 30px;
      }
    }
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    width: 100%;

    height: 100%;
    font-size: 14px;
    margin: 10px 0;

    @include for-phone {
      display: block;
      text-align: center;
      padding: 0;
      margin: 0;
      // height: 150px;
    }

    //menu right//
    .me {
      padding-left: 10px;
      padding-right: 10px;
    }

    .menu {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: crimson;

      @include for-phone {
        //flex-direction: column;
        display: block;
        background: $superlightgreycolor;
        width: 100%;
        align-items: left;
      }

      li {
        margin-right: 20px;
        display: inline-block;
        @include for-phone {
          flex-direction: column;
          padding: 20px;
          width: 100%;
          &:hover {
            background: $lightmintcolor;
          }
        }

        a {
          @include for-phone {
            display: block;
            padding-bottom: 10px;
          }
        }
      }
    }

    .fas {
      margin-right: 5px;
      font-size: 12px;

      @include for-phone {
        display: none;
      }
    }

    .search {
      button {
        width: 22px;
        height: 22px;
        background: $mintcolor;
        color: white;
        font-size: 12px;
        border: 0;
        border-left: none;
        cursor: pointer;
        &:hover {
          border: 1px solid $yellowcolor;
        }
      }
    }
  }
}

.menu-btn {
  display: none;

  @include for-phone {
    display: block;
    padding: 2vw;
    font-size: 5vh;
    background-color: $yellowcolor;
    border: none;
    align-self: flex-start;

    /*outline none removes the default blue border that appears anytime you click on the button*/
    outline: none;
    cursor: pointer;
  }
}

.logo-menu {
  @include for-phone {
    padding: 20px;
    background: $superlightgreycolor;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
